<template>
  <view class="post-order-page">
    <uni-forms ref="formRef" :modelValue="formData" :rules="rules" label-width="100">

      <!-- Âü∫Êú¨‰ø°ÊÅØ -->
      <view class="form-section">
        <view class="section-title">Âü∫Êú¨‰ø°ÊÅØ</view>
        <uni-forms-item label="ÈúÄË¶Å‰∫∫Êï∞" name="people_needed" required>
          <uni-easyinput
            v-model="formData.people_needed"
            type="number"
            placeholder="ËØ∑ËæìÂÖ•ÈúÄË¶Å‰∫∫Êï∞"
          />
        </uni-forms-item>

        <uni-forms-item label="ËßíËâ≤ÊèèËø∞" name="role_description">
          <uni-easyinput
            v-model="formData.role_description"
            type="textarea"
            placeholder="ËØ∑ÊèèËø∞ËßíËâ≤Ë¶ÅÊ±ÇÂíåÂ∑•‰ΩúÂÜÖÂÆπ"
            :inputBorder="false"
          />
        </uni-forms-item>
      </view>

      <!-- ÈõÜÂêà‰ø°ÊÅØ -->
      <view class="form-section">
        <view class="section-title">ÈõÜÂêà‰ø°ÊÅØ</view>
        <uni-forms-item label="ÈõÜÂêàÂú∞ÁÇπ" name="meeting_location_name" required>
          <view class="location-picker" @click="openMapPicker">
            <text v-if="formData.meeting_location_name" class="location-text">
              {{ formData.meeting_location_name }}
            </text>
            <text v-else class="placeholder-text">ÁÇπÂáªÈÄâÊã©ÈõÜÂêàÂú∞ÁÇπ</text>
            <uni-icons type="location" size="20" color="#007aff"></uni-icons>
          </view>
        </uni-forms-item>

        <uni-forms-item label="ÈõÜÂêàÊó∂Èó¥" name="meeting_time" required>
          <view class="datetime-picker-wrapper" @click="showDateTimePicker">
            <text v-if="formData.meeting_time" class="datetime-text">
              {{ formatTimestamp(formData.meeting_time) }}
            </text>
            <text v-else class="datetime-placeholder">ËØ∑ÈÄâÊã©ÈõÜÂêàÊó∂Èó¥</text>
            <uni-icons type="calendar" size="20" color="#999"></uni-icons>
          </view>
        </uni-forms-item>
      </view>

      <!-- Á≠õÈÄâÊù°‰ª∂ -->
      <view class="form-section">
        <view class="section-title">ÊºîÂëòË¶ÅÊ±Ç</view>
        <uni-forms-item label="ÊÄßÂà´Ë¶ÅÊ±Ç" name="gender_requirement">
          <uni-data-checkbox
            v-model="formData.gender_requirement"
            :localdata="genderOptions"
            mode="default"
          />
        </uni-forms-item>

        <uni-forms-item label="Ë∫´È´òËåÉÂõ¥" name="height_range">
          <view class="height-range">
            <uni-easyinput
              v-model="formData.height_min"
              type="number"
              placeholder="ÊúÄ‰Ωé"
              class="height-input"
            />
            <text class="range-separator">-</text>
            <uni-easyinput
              v-model="formData.height_max"
              type="number"
              placeholder="ÊúÄÈ´ò"
              class="height-input"
            />
            <text class="unit-text">cm</text>
          </view>
        </uni-forms-item>

        <uni-forms-item label="‰ΩìÂûãË¶ÅÊ±Ç" name="body_type">
          <uni-data-checkbox
            v-model="formData.body_type"
            :localdata="bodyTypeOptions"
            multiple
            mode="tag"
          />
        </uni-forms-item>

        <uni-forms-item label="ÁâπÈïøË¶ÅÊ±Ç" name="special_skills">
          <uni-data-checkbox
            v-model="formData.special_skills"
            :localdata="skillOptions"
            multiple
            mode="tag"
          />
        </uni-forms-item>
      </view>

      <!-- Á¶èÂà©Ê†áÁ≠æ -->
      <view class="form-section">
        <view class="section-title">Á¶èÂà©ÂæÖÈÅá</view>
        <uni-forms-item label="Á¶èÂà©Ê†áÁ≠æ" name="welfare_tags">
          <uni-data-checkbox
            v-model="formData.welfare_tags"
            :localdata="welfareOptions"
            multiple
            mode="tag"
            selectedColor="#52c41a"
          />
        </uni-forms-item>
      </view>

      <!-- ÂÆö‰ª∑ËÆæÁΩÆ -->
      <view class="form-section">
        <view class="section-title">ÂÆö‰ª∑ËÆæÁΩÆ</view>
        <uni-forms-item label="ËÆ°Ë¥πÊñπÂºè" name="price_type" required>
          <uni-data-checkbox
            v-model="formData.price_type"
            :localdata="priceTypeOptions"
            mode="default"
          />
        </uni-forms-item>

        <uni-forms-item label="ÈáëÈ¢ù" name="price_amount" required>
          <view class="price-input-wrapper">
            <uni-easyinput
              v-model="formData.price_amount"
              type="number"
              placeholder="ËØ∑ËæìÂÖ•ÈáëÈ¢ù"
            />
            <text class="unit-text">ÂÖÉ</text>
          </view>
          <view class="price-tip">
            <text class="tip-text">
              {{ formData.price_type === 'daily' ? 'ÊåâÂ§©ËÆ°Ë¥π' : 'ÊåâÂ∞èÊó∂ËÆ°Ë¥π' }}
            </text>
          </view>
        </uni-forms-item>
      </view>

      <!-- Â§áÊ≥® -->
      <view class="form-section">
        <view class="section-title">ÂÖ∂‰ªñ‰ø°ÊÅØ</view>
        <uni-forms-item label="Â§áÊ≥®" name="remark">
          <uni-easyinput
            v-model="formData.remark"
            type="textarea"
            placeholder="ÂÖ∂‰ªñË°•ÂÖÖËØ¥Êòé"
            :inputBorder="false"
          />
        </uni-forms-item>
      </view>

    </uni-forms>

    <!-- Êó•ÊúüÊó∂Èó¥ÈÄâÊã©ÂºπÁ™ó -->
    <uni-popup ref="dateTimePopup" type="bottom" class="datetime-popup-wrapper">
      <view class="datetime-popup">
        <view class="popup-header">
          <text class="popup-title">ÈÄâÊã©ÈõÜÂêàÊó∂Èó¥</text>
          <view class="popup-close" @tap="closeDateTimePicker">
            <uni-icons type="close" size="24" color="#666"></uni-icons>
          </view>
        </view>

        <view class="picker-content">
          <view class="picker-item">
            <text class="picker-label">Êó•Êúü</text>
            <picker mode="date" :value="tempDate" :start="minDate" @change="onDateChange">
              <view class="picker-value">
                <text>{{ tempDate || 'ËØ∑ÈÄâÊã©Êó•Êúü' }}</text>
                <uni-icons type="forward" size="16" color="#999"></uni-icons>
              </view>
            </picker>
          </view>

          <view class="picker-item">
            <text class="picker-label">Êó∂Èó¥</text>
            <picker mode="time" :value="tempTime" @change="onTempTimeChange">
              <view class="picker-value">
                <text>{{ tempTime || 'ËØ∑ÈÄâÊã©Êó∂Èó¥' }}</text>
                <uni-icons type="forward" size="16" color="#999"></uni-icons>
              </view>
            </picker>
          </view>
        </view>

        <view class="popup-footer">
          <button class="cancel-btn" @tap="closeDateTimePicker">ÂèñÊ∂à</button>
          <button class="confirm-btn" type="primary" @tap="confirmDateTime">Á°ÆÂÆö</button>
        </view>
      </view>
    </uni-popup>

    <!-- Êèê‰∫§ÊåâÈíÆ -->
    <view class="submit-section">
      <view class="order-type-tip">
        <text class="tip-icon">üí°</text>
        <text class="tip-text">{{ orderTypeTip }}</text>
      </view>
      <view v-if="formData.meeting_time" class="time-debug">
        <text>Â∑≤ÈÄâÊó∂Èó¥: {{ formatTimestamp(formData.meeting_time) }}</text>
      </view>
      <button class="submit-btn" type="primary" @click="handleSubmit">ÂèëÂ∏ÉÈúÄÊ±Ç</button>
    </view>

  </view>
</template>

<script>
export default {
  data() {
    return {
      // ‰∏¥Êó∂Êó•ÊúüÊó∂Èó¥
      tempDate: '',
      tempTime: '',
      minDate: '',

      formData: {
        people_needed: 1,
        role_description: '',
        meeting_location_name: '',
        meeting_location: null,
        meeting_time: null,
        gender_requirement: 0,
        height_min: '',
        height_max: '',
        body_type: [],
        special_skills: [],
        welfare_tags: [],
        price_type: 'daily',
        price_amount: '',
        remark: ''
      },

      // ÊÄßÂà´ÈÄâÈ°π
      genderOptions: [
        { value: 0, text: '‰∏çÈôê' },
        { value: 1, text: 'Áî∑' },
        { value: 2, text: 'Â•≥' }
      ],

      // ‰ΩìÂûãÈÄâÈ°π
      bodyTypeOptions: [
        { value: 'slim', text: 'ÂÅèÁò¶' },
        { value: 'normal', text: 'Ê†áÂáÜ' },
        { value: 'athletic', text: 'ÂÅ•Â£Æ' },
        { value: 'plus', text: 'ÂæÆËÉñ' }
      ],

      // ÁâπÈïøÈÄâÈ°π
      skillOptions: [
        { value: 'driving', text: '‰ºöÂºÄËΩ¶' },
        { value: 'dancing', text: '‰ºöË∑≥Ëàû' },
        { value: 'singing', text: '‰ºöÂî±Ê≠å' },
        { value: 'martial_arts', text: '‰ºöÊ≠¶ÊúØ' },
        { value: 'swimming', text: '‰ºöÊ∏∏Ê≥≥' },
        { value: 'riding', text: '‰ºöÈ™ëÈ©¨' },
        { value: 'instrument', text: '‰ºö‰πêÂô®' },
        { value: 'foreign_language', text: '‰ºöÂ§ñËØ≠' }
      ],

      // Á¶èÂà©Ê†áÁ≠æÈÄâÈ°π
      welfareOptions: [
        { value: 'meal', text: 'ÂåÖÁõíÈ•≠' },
        { value: 'taxi', text: 'ÂåÖÊù•ÂõûËΩ¶Ë¥π' },
        { value: 'accommodation', text: 'Êèê‰æõ‰ΩèÂÆø' },
        { value: 'tea', text: 'ÊúâÂ•∂Ëå∂/‰∏ãÂçàËå∂' },
        { value: 'other', text: 'ÂÖ∂‰ªñÁ¶èÂà©' }
      ],

      // ËÆ°Ë¥πÊñπÂºèÈÄâÈ°π
      priceTypeOptions: [
        { value: 'daily', text: 'ÊåâÂ§©' },
        { value: 'hourly', text: 'ÊåâÊó∂' }
      ],

      // Ë°®ÂçïÊ†°È™åËßÑÂàô
      rules: {
        people_needed: {
          rules: [
            { required: true, errorMessage: 'ËØ∑ËæìÂÖ•ÈúÄË¶Å‰∫∫Êï∞' },
            {
              validateFunction: (rule, value, data, callback) => {
                if (value < 1) {
                  callback('‰∫∫Êï∞Ëá≥Â∞ë‰∏∫1')
                }
                return true
              }
            }
          ]
        },
        meeting_location_name: {
          rules: [{ required: true, errorMessage: 'ËØ∑ÈÄâÊã©ÈõÜÂêàÂú∞ÁÇπ' }]
        },
        meeting_time: {
          rules: [
            { required: true, errorMessage: 'ËØ∑ÈÄâÊã©ÈõÜÂêàÊó∂Èó¥' },
            {
              validateFunction: (rule, value, data, callback) => {
                if (new Date(value).getTime() <= Date.now()) {
                  callback('ÈõÜÂêàÊó∂Èó¥ÂøÖÈ°ªÊôö‰∫éÂΩìÂâçÊó∂Èó¥')
                }
                return true
              }
            }
          ]
        },
        price_type: {
          rules: [{ required: true, errorMessage: 'ËØ∑ÈÄâÊã©ËÆ°Ë¥πÊñπÂºè' }]
        },
        price_amount: {
          rules: [
            { required: true, errorMessage: 'ËØ∑ËæìÂÖ•ÈáëÈ¢ù' },
            {
              validateFunction: (rule, value, data, callback) => {
                if (value <= 0) {
                  callback('ÈáëÈ¢ùÂøÖÈ°ªÂ§ß‰∫é0')
                }
                return true
              }
            }
          ]
        }
      }
    }
  },

  computed: {
    // ËÆ¢ÂçïÁ±ªÂûãÊèêÁ§∫
    orderTypeTip() {
      if (!this.formData.meeting_time) {
        return 'ËØ∑ÂÖàÈÄâÊã©ÈõÜÂêàÊó∂Èó¥'
      }
      const meetingTime = new Date(this.formData.meeting_time).getTime()
      const now = Date.now()
      const diffHours = (meetingTime - now) / (1000 * 60 * 60)

      if (diffHours < 2) {
        return 'Âç≥Êó∂Âçï:Êé•ÂçïÂêéÁ´ãÂç≥ÂºÄÂêØÂÆûÊó∂ÂÆö‰ΩçËøΩË∏™'
      } else {
        return 'È¢ÑÁ∫¶Âçï:ÊºîÂëòÂá∫ÂèëÂêéÂºÄÂêØÂÆö‰Ωç,‰øùÊä§ÈöêÁßÅÁúÅÁîµ'
      }
    }
  },

  mounted() {
    // ËÆæÁΩÆÊúÄÂ∞èÊó•Êúü‰∏∫‰ªäÂ§©
    const today = new Date()
    const year = today.getFullYear()
    const month = String(today.getMonth() + 1).padStart(2, '0')
    const day = String(today.getDate()).padStart(2, '0')
    this.minDate = `${year}-${month}-${day}`
  },

  methods: {
    // Ê†ºÂºèÂåñÊó∂Èó¥Êà≥
    formatTimestamp(timestamp) {
      if (!timestamp) return ''
      const date = new Date(timestamp)
      const year = date.getFullYear()
      const month = String(date.getMonth() + 1).padStart(2, '0')
      const day = String(date.getDate()).padStart(2, '0')
      const hour = String(date.getHours()).padStart(2, '0')
      const minute = String(date.getMinutes()).padStart(2, '0')
      return `${year}-${month}-${day} ${hour}:${minute}`
    },

    // ÊòæÁ§∫Êó•ÊúüÊó∂Èó¥ÈÄâÊã©Âô®
    showDateTimePicker() {
      // Â¶ÇÊûúÂ∑≤ÊúâÂÄºÔºåÂàùÂßãÂåñ‰∏¥Êó∂ÂÄº
      if (this.formData.meeting_time) {
        const date = new Date(this.formData.meeting_time)
        const year = date.getFullYear()
        const month = String(date.getMonth() + 1).padStart(2, '0')
        const day = String(date.getDate()).padStart(2, '0')
        const hour = String(date.getHours()).padStart(2, '0')
        const minute = String(date.getMinutes()).padStart(2, '0')
        this.tempDate = `${year}-${month}-${day}`
        this.tempTime = `${hour}:${minute}`
      } else {
        this.tempDate = ''
        this.tempTime = ''
      }
      this.$refs.dateTimePopup.open()
    },

    // ÂÖ≥Èó≠Êó•ÊúüÊó∂Èó¥ÈÄâÊã©Âô®
    closeDateTimePicker() {
      this.$refs.dateTimePopup.close()
    },

    // Êó•ÊúüÂèòÂåñ
    onDateChange(e) {
      this.tempDate = e.detail.value
    },

    // Êó∂Èó¥ÂèòÂåñ
    onTempTimeChange(e) {
      this.tempTime = e.detail.value
    },

    // Á°ÆËÆ§ÈÄâÊã©
    confirmDateTime() {
      if (!this.tempDate || !this.tempTime) {
        uni.showToast({
          title: 'ËØ∑ÈÄâÊã©ÂÆåÊï¥ÁöÑÊó•ÊúüÂíåÊó∂Èó¥',
          icon: 'none'
        })
        return
      }

      // ÁªÑÂêàÊó•ÊúüÂíåÊó∂Èó¥ - ‰ΩøÁî® iOS ÂÖºÂÆπÁöÑÊ†ºÂºè yyyy/MM/dd HH:mm:ss
      const dateTimeStr = `${this.tempDate.replace(/-/g, '/')} ${this.tempTime}:00`
      const timestamp = new Date(dateTimeStr).getTime()

      // È™åËØÅÊó∂Èó¥ÂøÖÈ°ªÊôö‰∫éÂΩìÂâçÊó∂Èó¥
      if (timestamp <= Date.now()) {
        uni.showToast({
          title: 'ÈõÜÂêàÊó∂Èó¥ÂøÖÈ°ªÊôö‰∫éÂΩìÂâçÊó∂Èó¥',
          icon: 'none'
        })
        return
      }

      this.formData.meeting_time = timestamp
      this.closeDateTimePicker()
    },

    // ÊâìÂºÄÂú∞ÂõæÈÄâÁÇπ
    openMapPicker() {
      uni.chooseLocation({
        success: async (res) => {
          // ‰ºòÂÖà‰ΩøÁî®name,Â¶ÇÊûúÊ≤°ÊúâÂàôÂ∞ùËØïÈÄÜÂú∞ÁêÜÁºñÁ†Å
          if (res.name) {
            this.formData.meeting_location_name = res.name
          } else if (res.address) {
            this.formData.meeting_location_name = res.address
          } else {
            // Ë∞ÉÁî®Âú∞ÁêÜÁºñÁ†ÅÊúçÂä°Ëé∑ÂèñËØ¶ÁªÜÂú∞ÂùÄ
            const address = await this.getAddressFromLocation(res.longitude, res.latitude)
            this.formData.meeting_location_name = address
          }

          this.formData.meeting_location = {
            type: 'Point',
            coordinates: [res.longitude, res.latitude]
          }
        },
        fail: (err) => {
          console.error('ÈÄâÊã©Âú∞ÁÇπÂ§±Ë¥•:', err)
          uni.showToast({
            title: 'ËØ∑ÊéàÊùÉ‰ΩçÁΩÆÊùÉÈôê',
            icon: 'none'
          })
        }
      })
    },

    // Ê†πÊçÆÂùêÊ†áËé∑ÂèñÂú∞ÂùÄ
    async getAddressFromLocation(longitude, latitude) {
      try {
        const res = await uniCloud.callFunction({
          name: 'geo-service',
          data: {
            action: 'regeo',
            longitude,
            latitude
          }
        })

        if (res.result && res.result.code === 0) {
          return res.result.data.name || res.result.data.address
        }
      } catch (error) {
        console.error('Ëé∑ÂèñÂú∞ÂùÄÂ§±Ë¥•:', error)
      }
      return `‰ΩçÁΩÆ (${longitude.toFixed(6)}, ${latitude.toFixed(6)})`
    },

    // Êèê‰∫§Ë°®Âçï
    async handleSubmit() {
      try {
        // Ë°®ÂçïÊ†°È™å
        await this.$refs.formRef.validate()

        // ÊòæÁ§∫Âä†ËΩΩ
        uni.showLoading({
          title: 'ÂèëÂ∏É‰∏≠...',
          mask: true
        })

        // ÂáÜÂ§áÊèê‰∫§Êï∞ÊçÆ
        const submitData = {
          ...this.formData,
          people_needed: parseInt(this.formData.people_needed),
          height_min: this.formData.height_min ? parseInt(this.formData.height_min) : 0,
          height_max: this.formData.height_max ? parseInt(this.formData.height_max) : 0,
          price_amount: Math.round(parseFloat(this.formData.price_amount) * 100) // ËΩ¨Êç¢‰∏∫ÂàÜ
        }

        console.log('=== Debug: submitData ===', submitData)

        // Ë∞ÉÁî®‰∫ëÂØπË±°
        const orderCo = uniCloud.importObject('order-co')

        // ÂÖàÊµãËØï token ÊòØÂê¶Ê≠£Â∏∏
        console.log('=== Debug: testing token ===')
        try {
          const tokenTest = await orderCo.testToken()
          console.log('=== Debug: testToken result ===', tokenTest)
        } catch (tokenErr) {
          console.error('=== Debug: testToken error ===', tokenErr)
        }

        console.log('=== Debug: calling add ===')
        const result = await orderCo.add(submitData)
        console.log('=== Debug: add result ===', result)

        uni.hideLoading()

        if (result.code === 0) {
          uni.showToast({
            title: 'ÂèëÂ∏ÉÊàêÂäü',
            icon: 'success'
          })

          // Âª∂ËøüË∑≥ËΩ¨Âà∞ËÆ¢ÂçïÂàóË°®
          setTimeout(() => {
            // Ëé∑ÂèñÂΩìÂâçÈ°µÈù¢Ê†à
            const pages = getCurrentPages()
            if (pages.length > 1) {
              // Â¶ÇÊûúÊúâ‰∏ä‰∏ÄÈ°µÔºåÂàôËøîÂõû
              uni.navigateBack({
                delta: 1
              })
            } else {
              // Â¶ÇÊûúÊ≤°Êúâ‰∏ä‰∏ÄÈ°µÔºåË∑≥ËΩ¨Âà∞ËÆ¢ÂçïÂàóË°®
              uni.redirectTo({
                url: '/pages/crew/order_list'
              })
            }
          }, 1500)
        } else {
          uni.showToast({
            title: result.message || 'ÂèëÂ∏ÉÂ§±Ë¥•',
            icon: 'none'
          })
        }

      } catch (err) {
        uni.hideLoading()
        console.error('Êèê‰∫§Â§±Ë¥•:', err)

        if (err.errMsg) {
          // Ë°®ÂçïÊ†°È™åÈîôËØØ
          uni.showToast({
            title: 'ËØ∑ÂÆåÂñÑÂøÖÂ°´‰ø°ÊÅØ',
            icon: 'none'
          })
        } else {
          uni.showToast({
            title: err.message || 'Êèê‰∫§Â§±Ë¥•',
            icon: 'none'
          })
        }
      }
    }
  }
}
</script>

<style lang="scss">
// ÂÖ®Â±ÄÊ†∑ÂºèÔºöÊèêÂçáÂºπÁ™óÂ±ÇÁ∫ß
.datetime-popup-wrapper {
  z-index: 10000 !important;
}
</style>

<style lang="scss" scoped>
.post-order-page {
  min-height: 100vh;
  background-color: #f5f5f5;
  padding-bottom: 20px;
}

// Ë°®ÂçïÂàÜÁªÑ
.form-section {
  margin-bottom: 15px;
  background-color: #fff;

  .section-title {
    padding: 15px;
    font-size: 15px;
    font-weight: 500;
    color: #333;
    border-bottom: 1px solid #e5e5e5;
  }
}

// Âú∞ÁÇπÈÄâÊã©Âô®
.location-picker {
  display: flex;
